<template lang="">
    <div class="inventory">
        <div class="container">
            <div class="inventory-main">
                <div class="inventory-main_filters">
                    <div class="filters-title">
                        <h3>Detailed search</h3>
                        <p @click="clearFilters">Clear filters</p>
                    </div>
                    <div class="filters-search">

                        <div class="filter_make-model">
                            <div class="select filter-select">
                                <div class="select__header" @click="selectModel = !selectModel">
                                    <span class="select__current">Make, Model</span>
                                    <div class="select__icon" :class="{activeImg: selectModel === true}">
                                        <img src="../assets/img/inventory/Vector3.png" alt="">
                                    </div>
                                </div>
                                <div class="select-option_done option_done-margin" v-if="selectModel === false && optionDonesMake.length">
                                    <div class="option_done-main" v-for="(optionDone, i) in optionDonesMake"
                                    :key="i">
                                        <img src="../assets/img/inventory/Close.png" @click="deleteDoneMake(i)">
                                        <span>{{optionDone}}</span>
                                    </div>
                                </div>
                                <div class="select-option_done option_done-margin" v-if="selectModel === false && optionDonesMadel.length">
                                    <div class="option_done-main" v-for="(optionDone, i) in optionDonesMadel"
                                    :key="i">
                                        <img src="../assets/img/inventory/Close.png" @click="deleteDoneMadel(i)">
                                        <span>{{optionDone}}</span>
                                    </div>
                                </div>

                                <div class="select__body-model" v-if="selectModel === true">
                                    <div class="body-model_main">
                                        <h4 class="model-title">Make</h4>
                                        <input type="text" v-model="searchMake" class="inventory-search filter-select"
                                        placeholder="Search Make...">
                                        <div class="select-option" v-if="searchMake != ''">
                                            <div class="select-option_name" v-for="(make, i) in filterMake" :key="i" 
                                            @click="selectMakeClick(make)" v-if="filterMake.length">
                                                {{make.name}}
                                            </div>
                                            <div class="select-option_name" v-else>
                                                Nothing found
                                            </div>
                                        </div>
                                        <div class="select-option_done">
                                            <div class="option_done-main" v-for="(optionDone, i) in optionDonesMake"
                                            :key="i">
                                            <img src="../assets/img/inventory/Close.png" @click="deleteDoneMake(i)">
                                            <span>{{optionDone}}</span>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="body-model_main">
                                        <h4 class="model-title">Model</h4>
                                        <input type="text" v-model="searchModel" class="inventory-search filter-select"
                                        placeholder="Search Model...">
                                        <div class="select-option" v-if="searchModel != ''">
                                            <div class="select-option_name" v-for="(madel, i) in filterMadel" :key="i"
                                            v-if="filterMadel.length" @click="selectMadelClick(madel)">
                                                {{madel.name}}
                                            </div>
                                            <div class="select-option_name" v-else>
                                                Nothing found
                                            </div>
                                        </div>
                                        <div class="select-option_done">
                                            <div class="option_done-main" v-for="(optionDoneMadel, i) in optionDonesMadel"
                                            :key="i">
                                                <img src="../assets/img/inventory/Close.png" @click="deleteDoneMadel(i)">
                                                <span>{{optionDoneMadel}}</span>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>


                        <div class="filter_make-model">
                            <div class="select filter-select">
                                <div class="select__header" @click="bodyType = !bodyType">
                                    <span class="select__current">Body type</span>
                                    <div class="select__icon" :class="{activeImg: bodyType === true}">
                                        <img src="../assets/img/inventory/Vector3.png" alt="">
                                    </div>
                                </div>
                                <div class="select-option_done option_done-margin" v-if="bodyType === false && optionBodyType.length">
                                    <div class="option_done-main" v-for="(optionType, i) in optionBodyType"
                                    :key="i">
                                        <img src="../assets/img/inventory/Close.png" @click="deleteType(optionType, i)">
                                        <span>{{optionType.name}}</span>
                                    </div>
                                </div>

                                <div class="select__body-type" v-if="bodyType === true">
                                    <div class="body-model_type">
                                        <div class="select-type" v-for="(type, i) in selectBodyType" :key="i">
                                            <div class="select-option_type" @click="inputCheckboxClick(type, i)">
                                                <input type="checkbox" v-model="type.checked" :id="type.id">
                                                <label :for="type.id">
                                                <img :src="type.img">
                                                {{type.name}}
                                                </label>
                                            </div>
                                            
                                        </div>
                                        <div class="select-option_done">
                                            <div class="option_done-main" v-for="(optionType, i) in optionBodyType"
                                            :key="i">
                                            <img src="../assets/img/inventory/Close.png" @click="deleteType(optionType, i)">
                                            <span>{{optionType.name}}</span>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>

                    </div>
                </div>







                <div class="inventory-main_search">
                    <div class="search-left">
                        <input type="text" v-model="search" class="inventory-search" placeholder="Find a dream car...">
                        <img src="../assets/img/inventory/Share2.png" alt="">
                    </div>
                    <div class="search-right">
                        <p>Sorted by</p>
                        <div class="select" @click="selectBody = !selectBody">
                            <div class="select__header">
                            <span class="select__current">{{selected}}</span>
                            <div class="select__icon" :class="{activeImg: selectBody === true}">
                                <img src="../assets/img/inventory/Vector3.png" alt="">
                            </div>
                            </div>

                            <div class="select__body" v-if="selectBody === true">
                                <div class="select__item" v-for="(option, i) in options" :key="i" @click="selectOption(option)">
                                    {{option.name}}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>






                <div class="inventory-main_cars">
                    <div class="inventory-main_car">
                        <div class="inventory-card" v-for="(inventoryCar, i) in paginatedCars" :key="i"
                        v-if="paginatedCars.length >= 1">
                        <div class="inventory-card_top">
                            <swiper
                                :scrollbar="{
                                hide: false,
                                }"
                                :modules="modules"
                                class="mySwiper6"
                            >
                                <swiper-slide class="inventory-slide">
                                    <img :src="inventoryCar.exterior.image">
                                </swiper-slide>
                                <swiper-slide class="inventory-slide">
                                    <img :src="inventoryCar.exterior.image2">
                                </swiper-slide>
                                <swiper-slide class="inventory-slide">
                                    <img :src="inventoryCar.exterior.image3">
                                </swiper-slide>
                            </swiper>
                        </div>
                        <div class="inventory-card_bottom" @click="cartClick(inventoryCar.id)">
                            <h3>{{inventoryCar.titleCar}}</h3>
                            <p>$ {{inventoryCar.price}} 000</p>
                            <div class="cars-information">
                                <span>{{inventoryCar.year}} year</span>
                                <img src="../assets/img/inventory/Vector.png">
                                <span>{{inventoryCar.body}}</span>
                                <img src="../assets/img/inventory/Vector.png">
                                <span>{{inventoryCar.transmission}}</span>
                            </div>
                            <span>{{inventoryCar.kilometres}} 000</span>
                        </div>
                    </div>
                    <div class="inventory-cars_none" v-else="paginatedCars.length < 1">
                        <div class="none-main">
                            <img src="../assets/img/inventory/Sedan.png" alt="">
                            <h3>Unfortunately there are no matches for your query.</h3>
                            <p>Try using other filter settings or request a car of your choice.</p>
                            <router-link to="">Request a car</router-link>
                        </div>
                    </div>
                </div>
                    <div class="btn-toolbar" v-if="paginatedCars.length > 1">
                        <div class="btn-group">
                            <button v-for="p in totalPaginas" @click='pageClick(p)' class="buttonP" 
                            :class="isActive(p)">
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>

// Import Swiper Vue.js components
import { Swiper, SwiperSlide } from "swiper/vue";

// Import Swiper styles
import "swiper/css";

import "swiper/css/scrollbar";

// import required modules
import { Scrollbar } from "swiper";

export default {
    data(){
        return{
            perPage: null,
            // dataPagination: [],
            // paginaActual: 1,
            search: '',
            pageNumber: 1,
            res: null,
            selectBody: false,
            selectModel: false,
            bodyType: false,
            searchMake: '',
            searchModel: '',
            options:[
                {name: 'Recommendations', id: 1},
                {name: 'Newest inventory', id: 2},
                {name: 'Lowest price', id: 3},
                {name: 'Highest price', id: 4},
            ],
            selectMake: [
                {name: 'Porche', id: 1},
                {name: 'Mitsubishi', id: 2},
                {name: 'Mercedes-Benz', id: 3},
                {name: 'Audi', id: 4},
            ],
            selectMadel: [
                {name: 'Panamera', id: 1},
                {name: 'Outlander', id: 2},
                {name: 'CLA I', id: 3},
                {name: 'W124', id: 4},
                {name: 'E-Class III', id: 5},
                {name: 'A7', id: 6},
            ],
            selectBodyType: [
                {id: 1, name: 'Trucks', img: require('@/assets/img/inventory/BodyType/Truck.png'), checked: false},
                {id: 2, name: 'SUV', img: require('@/assets/img/inventory/BodyType/SUV.png'), checked: false},
                {id: 3, name: 'Sedan', img: require('@/assets/img/inventory/BodyType/Sedan.png'), checked: false},
                {id: 4, name: 'Hatchback', img: require('@/assets/img/inventory/BodyType/Hatchback.png'), checked: false},
                {id: 5, name: 'Coupe', img: require('@/assets/img/inventory/BodyType/Coupe.png'), checked: false},
                {id: 6, name: 'Convertiable', img: require('@/assets/img/inventory/BodyType/Convertiable.png'), checked: false},
                {id: 7, name: 'VAN', img: require('@/assets/img/inventory/BodyType/VAN.png'), checked: false}
            ],
            selected: 'Recommendations',
            optionDonesMake: [],
            optionDonesMadel: [],
            optionBodyType: [],
        }
    },

    computed: {
        inventoryCars(){
            return this.$store.getters.inventoryCars
        },
        // searchHandler(){
        //     this.paginatedCars = this.inventoryCars
        //     console.log(this.paginatedCars)
        //     return this.paginatedCars.filter(elem => {
                
        //         return elem.titleCar.toUpperCase().includes(this.search.toUpperCase())
        //     })
        // },
        totalPaginas(){
            if(this.search === ''){
               return Math.ceil(this.inventoryCars.length / this.perPage)  
            } else if(this.res.length <= this.perPage){
                this.pageNumber = 1
                console.log(this.res.length <= this.perPage)
                return 1
            } else{
                return Math.ceil(this.res.length / this.perPage)
            } 
        },
        paginatedCars(){
            let from = Math.ceil(this.pageNumber - 1) * this.perPage;
            let to = from + this.perPage;
            window.scrollTo(0,0) 
            if(this.search === ''){
                return this.inventoryCars.slice(from, to)
            }else{
                this.res = this.inventoryCars.filter(elem => {
                    return elem.titleCar.toUpperCase().includes(this.search.toUpperCase())
                })
                return this.res.slice(from, to)
            } 
            // if(this.optionDonesMake.length < 1){
            //     return this.inventoryCars.slice(from, to)
            // }else if(this.optionDonesMake.length >= 1){
            //     this.res = this.inventoryCars.filter(elem => {
            //         return elem.titleCar.toUpperCase().includes(this.optionDonesMake.toUpperCase())
            //     })
            //     return this.res.slice(from, to)
            // }
            
        },
        filterMake(){
            if(this.searchMake === ''){
                return this.selectMake
            } else{
                return this.selectMake.filter(elem => {
                    return elem.name.toUpperCase().includes(this.searchMake.toUpperCase())
                })
            }
            
        },
        filterMadel(){
            if(this.searchModel === ''){
                return this.selectMadel
            } else{
                return this.selectMadel.filter(elem => {
                    return elem.name.toUpperCase().includes(this.searchModel.toUpperCase())
                })
            }
        },
    },

    methods:{
        innerWidth(){
            this.width = window.innerWidth;
            if(this.width > 576){
                this.perPage = 6
            }
            if(this.width <= 576){
                this.perPage = 4
            }
        },
        pageClick(p){
            this.pageNumber = p;
        },
        cartClick(id){
            this.$router.push({name: 'InventoryCar', query: {'inventory': id}})
        },
        isActive(p){
            return p == this.pageNumber ? 'activeP' : ''
        },
        selectOption(option){
            this.selected = option.name
            if(this.selected === 'Recommendations'){
                this.inventoryCars.sort((a, b) => a.id - b.id)
            } else if(this.selected === 'Newest inventory'){
                this.paginatedCars = []
                this.inventoryCars.sort((a, b) => b.year - a.year)
            } else if(this.selected === 'Lowest price'){
                this.inventoryCars.sort((a, b) => a.price - b.price)
            } else if(this.selected === 'Highest price'){
                this.inventoryCars.sort((a, b) => b.price - a.price)
            }
        },
        clearFilters(){
            this.optionDonesMadel = []
            this.optionDonesMake = []
            this.optionBodyType = []
        },
        selectMadelClick(madel){
            if(!this.optionDonesMadel.includes(madel.name)){
               this.optionDonesMadel.push(madel.name) 
            }
            this.searchModel = ''
        },
        selectMakeClick(make){
            if(!this.optionDonesMake.includes(make.name)){
               this.optionDonesMake.push(make.name) 
            }
            this.searchMake = ''
        },
        inputCheckboxClick(type, i){
            if(!this.optionBodyType.includes(type) && type.checked === false){
               this.optionBodyType.push(type)
            } else {
                type.checked = true
            }
            // console.log(this.optionBodyType)
            //type.checked = !type.checked
            //console.log(type.checked)
            // if(this.optionBodyType.checked === true){
            //     this.optionBodyType.splice(this.optionBodyType.id, 1)
            // }
        },
        deleteDoneMake(i){
            this.optionDonesMake.splice(i, 1)
        },
        deleteDoneMadel(i){
            this.optionDonesMadel.splice(i, 1)
        },
        deleteType(optionType, i){
            this.optionBodyType.splice(i, 1)
            optionType.checked = false
        },
        
    },

    created() {
        window.addEventListener('resize', this.innerWidth);
        this.innerWidth();
    },

    // mounted() {
    //     this.getDataPaginas(1);
    // },

    watch:{
        perPage(){
            if(this.width > 576){
                this.perPage = 6
            }
            if(this.width <= 576){
                this.perPage = 4
            }
            //this.getDataPaginas(1)
        }
    },

    components: {
        Swiper,
        SwiperSlide,
    },
    setup() {
        return {
        modules: [Scrollbar],
        };
    },
    
}
</script>
<style lang="scss">

@import '../assets/scss/breakpoints.scss';

.inventory{
    margin-top: 40px;
}
.inventory-main{
    display: grid;
    grid-template-columns: 30% 70%;
    grid-template-areas: 'filters search' 'filters cars';
}

                                                //Filters

.inventory-main_filters {
    grid-area: filters;
    width: 90%;
}
.filters-title {
    display: flex;
    align-items: center;
    gap: 20px;
    height: 45px;

    h3{
        font-family: 'Gilroy600';
        font-size: 20px;
        line-height: 25px;
        color: #41456B;
    }
    p{
        font-family: 'Gilroy500';
        font-size: 16px;
        line-height: 159%;
        color: #7481FF;
        margin-top: 3px;
        cursor: pointer;
    }
}
.filters-search {
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin-top: 30px;
}
.filter_make-model {
}
.select__body-model {
    display: flex;
    flex-direction: column;
    gap: 20px;
    margin-top: 30px;
}
.body-model_main {
    display: flex;
    flex-direction: column;
}
.model-title {
    font-family: 'Gilroy600';
    font-size: 16px;
    line-height: 14px;
    color: #41456B;
    margin-bottom: 10px;
}
.select-option {
    display: flex;
    flex-direction: column;
    gap: 10px;
    padding: 20px;
    background: #FFFFFF;
    border: 1px solid #D7D7D7;
    border-radius: 2px;
    border-top: 0px;
    overflow-y: auto;
    max-height: 155px;
}
.select-option_name {
    font-family: 'Gilroy600';
    font-size: 16px;
    line-height: 14px;
    color: #41456B;
    cursor: pointer;
}

.select-option_done {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    margin-top: 10px;
}
.option_done-main {
    display: flex;
    align-items: center;
    padding: 10px;
    gap: 10px;
    background: rgba(116, 129, 255, 0.2);
    border-radius: 2px;

    img{
        cursor: pointer;
    }

    span{
        font-family: 'Gilroy700';
        font-size: 16px;
        line-height: 14px;
        text-align: center;
        letter-spacing: 0.02em;
        text-transform: uppercase;
        color: #41456B;
    }
}
.option_done-margin{
    margin-top: 20px;
    margin-bottom: 10px;
}
.select__body-type{
    display: flex;
    flex-direction: column;
    margin-top: 20px;
}
.body-model_type{
    display: flex;
    flex-direction: column;
    gap: 10px;
}
.select-type{
    
}
.select-option_type{
    display: flex;
    align-items: center;

    input{
        display: none;
        cursor: pointer;
        
        &:checked + label::before{
            background-image: url(../assets/img/inventory/BodyType/Vector.png);
            background-repeat: no-repeat;
            background-position: 50%;
            cursor: pointer;
        }
    }
    label{
        display: flex;
        align-items: center;
        padding-left: 30px;
        font-family: 'Gilroy500';
        font-size: 16px;
        line-height: 159%;
        color: #41456B;
        gap: 5px;
        cursor: pointer;

        &::before{
            content: '';
            display: inline-block;
            width: 20px;
            height: 20px;
            border: 1px solid #D7D7D7;
            border-radius: 2px;
            margin-left: -30px;
            margin-right: 5px;
        }
    }
    
    
}

                                            //Search

.inventory-main_search {
    grid-area: search;
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
    position: relative;
}
.search-left{
    display: flex;
    align-items: center;
    gap: 20px;

    img{
        cursor: pointer;
    }
}
.inventory-search{
    width: 350px;
    height: 45px;
    font-family: 'Gilroy500';
    font-size: 16px;
    line-height: 159%;
    color: #606276;
    padding: 11px 20px;
    background-image: url(../assets/img/inventory/Search.png);
    background-position: right 20px center;
    background-repeat: no-repeat;
    border: 1px solid #D7D7D7;
    border-radius: 2px;
}
.search-right {
    display: flex;
    gap: 20px;
    position: absolute;
    right: 0;

    p{
        font-family: 'Gilroy400';
        font-size: 16px;
        line-height: 180%;
        letter-spacing: 0.02em;
        color: #41456B;
        margin-top: 10px;
    }
}
.select {
    border: 1px solid #D7D7D7;
    border-radius: 2px;
    width: 240px;
    padding: 10px 20px;
    background: #FFFFFF;
    z-index: 2;
    transition: all .3s ease;
}
.select__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;

    span{
        font-family: 'Gilroy500';
        font-size: 16px;
        line-height: 159%;
        color: #41456B;
    }
}
.select__icon {
    transition: all .3s ease;
}
.activeImg{
    transform: rotate(180deg);
    transition: all .3s ease;
}
.select__body {
    margin-top: 20px;
}

.select__item {
    font-family: 'Gilroy500';
    font-size: 16px;
    line-height: 159%;
    color: #41456B;
    margin-top: 10px;
    cursor: pointer;
}








                                            //Cars
.inventory-main_cars {
    grid-area: cars;
    display: flex;
    flex-direction: column;
    gap: 40px;

    .inventory-main_car{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        gap: 20px; 
    }
}


.inventory-card {
    width: 48%;
}
.inventory-card_top {
}
.mySwiper6 {

    .swiper-scrollbar-horizontal{
        position: absolute;
        left: 5%;
        bottom: 10px;
        width: 90%;
        background: hsla(0,0%,100%,.2);
    }
    .swiper-scrollbar-drag{
        background: #FFFFFF;
        opacity: 0.7;
        border-radius: 28px;
    }
    .swiper-wrapper{
        gap: 0;
    }
}
.inventory-slide {

    img{
        width: 100%;
        aspect-ratio: 1/.515;
        object-fit: cover;
    }
}
.inventory-card_bottom {
    padding: 20px;
    box-shadow: 0px 20px 25px rgba(0, 0, 0, 0.1);
    border-radius: 0px 0px 5px 5px;
    cursor: pointer;

    h3, p{
        font-family: 'Gilroy600';
        font-size: 20px;
        line-height: 25px;
        color: #41456B;
    }
    p{
        color: #7481FF;
        margin-top: 10px;
    }
    span{
        font-family: 'Gilroy400';
        font-size: 16px;
        line-height: 180%;
        letter-spacing: 0.02em;
        color: #41456B;
    }
}
.cars-information {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 20px;
    margin-top: 20px;
}


.inventory-cars_none {
    box-shadow: 0px 20px 25px rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    padding: 60px;
    width: 100%;
}
.none-main {
    display: flex;
    flex-direction: column;
    align-items: baseline;

    h3{
        font-family: 'Gilroy600';
        font-size: 20px;
        line-height: 25px;
        color: #41456B;
        margin-top: 20px;
    }
    p{
        font-family: 'Gilroy400';
        font-size: 16px;
        line-height: 180%;
        letter-spacing: 0.02em;
        color: #41456B;
        margin-top: 10px;
    }
    a{
        font-family: 'Gilroy700';
        font-size: 16px;
        line-height: 14px;
        letter-spacing: 0.02em;
        text-transform: uppercase;
        color: #7481FF;
        margin-top: 20px;
